<template>
  <div>
    <LayoutBox one="通知">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="全部消息" value="0"></el-tab-pane>
        <el-tab-pane label="发送消息" value="1"></el-tab-pane>
        <el-tab-pane label="交易消息" value="2"></el-tab-pane>
        <el-tab-pane label="退款消息" value="3"></el-tab-pane>
        <el-tab-pane label="商品消息" value="4"></el-tab-pane>
        <el-tab-pane label="处罚消息" value="5"></el-tab-pane>
      </el-tabs>
      <el-radio v-model="init.radio" label="0">全部</el-radio>
      <el-radio v-model="init.radio" label="1">已读</el-radio>
      <el-radio v-model="init.radio" label="2">未读</el-radio>
      <div v-for="item in messageList" :key="item.id" class="card_box" :style="selectMessage === item.id ? 'height: 200px' : ''" @click="onSelectMessage(item.id)">
        <div class="head">
          <div>
            <!-- 0-所有用户 1-商家 2-小超 3-区级代理 4-普通用户 -->
            <span>接收人：<span>{{
              item.type === 1 ? '商家' :
              item.type === 2 ? '小超' :
              item.type === 3 ? '区级代理' :
              item.type === 4 ? '普通用户' : '所有用户' }}
            </span></span>
            <span class="ml-1">发送人：<span>平台管理员</span></span>
          </div>
          <span>2020-03-20 11:44:31</span>
        </div>
        <div v-if="selectMessage === item.id">
          <div class="divider"></div>
          <span class="card_box_context">{{ item.context }}</span>
        </div>
      </div>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  components: {
    LayoutBox
  },

  data () {
    return {
      activeName: '0',
      init: {}, // 初始化查询条件
      selectMessage: '', // 选择哪条消息
      queryInit: {}, // 分页初始化
      total: 0, // 分页总条数
      messageList: [], // 消息列表
    }
  },

  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    },
    // 选择每页显示多少条
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
      this.apiAnnounceQuery()
    },
    // 选择第几页
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
      this.apiAnnounceQuery()
    },
    // 选择哪条消息
    onSelectMessage (i) {
      console.log('i, i', i)
      this.selectMessage = i
    },

    // 查询我的公告
    apiAnnounceQuery () {
      let data = {
        ...this.queryInit
      }
      this.$http.post('/woho/announce/query', data)
        .then((res) => {
          if (res.data.success) {
            this.total = res.data.data.total
            this.messageList = res.data.data.list
          } else { this.$message.error(res.data.msg) }
        })
    },
  },

  created () {
    this.apiAnnounceQuery()
  }
}
</script>

<style lang="scss" scoped>
  .card_box{
    overflow: hidden;
    width: 100%;
    height: 35px;
    transition: height 0.4s;
    -moz-transition: height 0.4s; /* Firefox 4 */
    -webkit-transition: height 0.4s; /* Safari and Chrome */
    -o-transition: height 0.4s; /* Opera */
    margin-top: 15px;
    box-shadow: 0px 3px 10px #e7e2e2;
    padding: 0px 20px;
    border-radius: 7px;
    .head{
      height: 35px;
      line-height: 35px;
      font-size: 14px;
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    .divider{
      height: 1px;
      border: 0.5px solid rgb(236, 231, 231);
    }
    .card_box_context{
      padding: 11px 0px;
      display: block;
      font-size: 13px;
    }
  }
</style>
